___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (min-width: 650px) {
    .app-bar {
      grid-template-columns: 1fr auto;
      padding: 8px 32px;
    }
   
    .app-bar .app-bar__menu {
      display: none;
    }
   
    .app-bar .app-bar__navigation {
      position: static;
      width: 100%;
    }
   
    .app-bar .app-bar__navigation ul li {
      display: inline-block;
    }
   
    .app-bar .app-bar__navigation ul li a {
      display: inline-block;
      width: 120px;
      text-align: center;
      margin: 0;
    }
   
    .restaurants {
      grid-template-columns: 1fr 1fr;
    }
   
    .restaurant .resto__name {
      grid-column-start: 1;
      grid-column-end: 3;
    }
   
    .restaurant .resto__review {
      grid-column-start: 1;
      grid-column-end: 3;
    }
  }
   
   
  @media screen and (min-width: 850px) {
    .restaurants {
      grid-template-columns: repeat(3, 1fr);
    }
  }
   
  @media screen and (min-width: 1200px) {
    .restaurants {
      grid-template-columns: repeat(4, 1fr);
    }
  }
   
  @media screen and (min-width: 1600px) {
    .restaurants {
        grid-template-columns: repeat(5, 1fr);
    }
  }

  @media screen and (min-width: 768px) {
    /* Tablet */
    .app-bar__brand h1 {
        font-size: 28px; 
    }
  }

  @media screen and (max-width: 368px) {
    /* Tablet */
    .app-bar__brand h1 {
        font-size: 15px; 
    }
  }

  @media screen and (min-width: 750px) and (max-width: 859px) {
    /* Tablet */
    .app-bar__brand h1 {
        font-size: 19px; 
    }
  }

  @media screen and (min-width: 690px) and (max-width: 749px) {
    /* Tablet */
    .app-bar__brand h1 {
        font-size: 14px; 
    }
  }

  @media screen and (min-width: 650px) and (max-width: 690px) {
    /* Tablet */
    .app-bar__brand h1 {
        font-size: 10px; 
    }
  }

  
  @media screen and (min-width: 1024px) {
    /* Desktop */
    .app-bar__brand h1 {
        font-size: 32px; 
    }
  }
