___CSS_LOADER_EXPORT___.push([module.id, `.hero_header {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 300px;
  }
  
  .hero_header img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    z-index: -1;
  }
  
  .hero_inner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #fff;
    padding: 0 20px; 
  }
  
  .hero_title {
    margin-bottom: 20px;
    font-size: 24px; 
  }
  
  .hero_tagline {
    margin-top: 20px;
    font-size: 16px; 
  }
  
  @media screen and (min-width: 320px) {
    .hero_header {
      height: 200px; 
    }
  
    .hero_title {
      font-size: 10px; 
    }
  
    .hero_tagline {
      font-size: 14px; 
    }
  }

  @media screen and (max-width: 768px) {
    .hero_title {
      font-size: 20px; 
    }
  }

